.splash {
    width: 100vw;
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.splash img {
    width: 10rem;
}

.app {
    width: 100%;
    height: 100%;
}

.container {
    width: 80%;
    margin: auto;
}

.download {
    font-family: 'Ubuntu', serif;
    padding: .66rem .9rem;
    display: flex;
    justify-content: space-between;
    background-color: #eeb600;
}

.download a {
    color: #000000;
    font-weight: 500;
    text-decoration: none;
}

.download button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    cursor: pointer;
}

.download button img {
    width: 1.1rem;
}

.brandIcon {
    margin-top: 1.5rem;
    text-align: center;
}

.brandIcon img {
    width: 6rem;
}

.addItems {
    margin-top: 1.5rem;
    position: relative;
    display: flex;
    align-items: center;
}

.addItems input {
    width: 100%;
    font-family: 'Ubuntu', serif;
    padding: .66rem 2.5rem .66rem .9rem;
    border-radius: .36rem;
}

.addItems button {
    width: 1.8rem;
    height: 1.8rem;
    position: absolute;
    right: .26rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: .3rem;
    background-color: #6000ff;
    cursor: pointer;
}

.addItems button img {
    width: 1.1rem;
}

.showItems {
    margin-top: 1.5rem;
    max-height: 50vh;
    overflow: scroll;
}

.item {
    margin-top: .5rem;
    color: #ffffff;
    font-family: 'Ubuntu', serif;
    position: relative;
    padding: .55rem .9rem;
    border-radius: .36rem;
    background-color: #6000ff;
}

.item button {
    width: 1.8rem;
    height: 1.8rem;
    position: absolute;
    top: .23rem;
    right: .26rem;
    display: none;
    align-items: center;
    justify-content: center;
    border-radius: .3rem;
    background-color: #6000ff;
    cursor: pointer;
}

.item button img {
    width: 1.1rem;
}

.item:hover {
    color: #6000ff;
    background-color: #ffffff;
}

.item:hover button {
    display: flex;
}

.box {
    width: fit-content;
    margin: auto;
    margin-top: 1.5rem;
    display: flex;
    gap: 1rem;
}

.box button {
    width: fit-content;
    color: #ffffff;
    font-family: 'Ubuntu', serif;
    padding: .55rem .9rem;
    border-radius: .36rem;
    background-color: #ee9600;
    cursor: pointer;
}

.box button:hover {
    background-color: #eeb600;
}

@media only screen and (width >= 561px ) {
    .container {
        width: 60%;
    }

    .brandIcon img {
        width: 8rem;
    }
}

@media only screen and (width >= 991px ) {
    .container {
        width: 40%;
    }
}


