@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input, button {
  border: none;
  outline: none;
}

body {
  background-color: #0d0020;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 0;
}